<template lang="html">
  <div class="limits-selector">
    <caption>Standing orders per page</caption>

    <multiselect
      v-model="currentLimit"
      :options="limits"
      :searchable="false"
      :show-labels="false"
      :allow-empty="false"
      track-by="value"
      label="name"
      placeholder=""
      :class="cssClass"
    />
  </div>
</template>

<script>
export default {
  name: 'limits-selector',

  props: {
    count: Number,
    onLimitChange: Function,
    cssClass: String
  },

  computed: {
    currentLimit: {
      get () {
        const v = this.count
        return { value: v, name: v }
      },

      set ({ value }) {
        this.onLimitChange && this.onLimitChange(value)
      }
    }
  },

  data () {
    return {
      limits: [ 10, 50, 100, 200 ].map((v) => {
        return { value: v, name: v }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.limits-selector {
  display: flex;
  align-items: center;

  .multiselect {
    margin-left: 16px;
    width: 96px;
  }
}
</style>



