<template>
    <div class="content-card transaction-card">
      <div class="info-row columns is-hidden-mobile">
          <div class="level">
            <div class="date read">{{date}}</div>
          </div>

          <div class="level">
            <div class="recipient">{{recipient}}</div>
          </div>
          
          <div class="level">
            <span class="type">{{type}}</span>
          </div>

          <div class="level">
            <div class="refNumber">74</div>
          </div>

          <div class="level-right">
            <div class="debit">-15000000</div>
          </div>
      </div>

      <div class="mobile-info-row columns is-hidden-tablet is-hidden-desktop pending">
          <div class="column">
            <div class="date">{{date}}</div>
            <div class="debit">-15000000</div>
          </div>

          <div class="column">
            <div class="recipient">{{recipient}}</div>
            <div class="refNumber">74</div>
          </div>
          
          <div class="column">
            <span>Status:</span>
            <div class="status pending">
              <svgicon name="pending_transaction"></svgicon>
              <span>Pending</span>
            </div>
          </div>
      </div>

      <div class="level content" :class="{closed: !opened}">
        <div class="level-left transaction-info">
          <div class="row" @click="opened = !opened">
            <div class="text-body1">Transaction details</div>
            <div class="arrow is-hidden-tablet is-hidden-desktop" :class="{down: !opened, up: opened}"></div>
          </div>

          <ul class="info">
            <li>
              <div class="text-body2">Value Date: {{valueDate}}</div>
            </li>
            <li>
              <div class="text-body2">Remitter Name: {{remitterName}}</div>
            </li>
            <li>
              <div class="text-body2">Remitter IBAN: {{remitterIBAN}}</div>
            </li>
            <li v-if="typeOfTransaction === TYPE_TRANSACTIONS.INTERNAL">
              <div class="text-body2">Recipient Name: {{recipientName}}</div>
            </li>
            <li v-if="typeOfTransaction === TYPE_TRANSACTIONS.INTERNAL">
              <div class="text-body2">Recipient Email: {{recipientEmail}}</div>
            </li>
          </ul>
        </div>

        <div class="level is-hidden-desktop is-hidden-tablet details">
          <button class="main-btn second">View details</button>
        </div>

        <div class="level-right users-info">
          <div class="text-body1" v-if="status === 'PENDING'">Pending authorisation:</div>
          <div class="text-body1" v-if="status === 'AUTHORISED'">Authorised by:</div>


          <a class="profile">
            <span class="text-body2">{{remitterName}}</span>
            <svgicon name="profile" width="24px" height="24px"></svgicon>
          </a>
        </div>

      </div>

      <template v-if="this.status !== 'AUTHORISED'">
        <div v-if="!authoriseActive" class="controls">
          <button class="main-btn primary" @click="authoriseActive = true">Authorise transaction</button>
        </div>

        <div v-else class="auth-container">
          <div class="columns">
            <h5 class="column is-narrow">Waiting for push notification</h5>
            <div class="column is-narrow">
              *spinner*
            </div>
          </div>

          <div class="columns inputs">
            <div class="column is-narrow passcode input">
              <h5 class="text">or</h5>
              <input id="passcode" name="passcode" type="text" placeholder="Enter passcode" v-model="passcode" v-validate="{required: true}" :class="{ 'error': errors.has('passcode') }">
              <span v-show="errors.has('passcode')">The field is invalid</span>
            </div>
            <button class="column is-narrow main-btn second" :disabled="!passcode || errors.has('passcode')" @click="authorise">Authorise transaction</button>
            <button class="column is-narrow main-btn second" @click="authoriseLater">Authorise later</button>
          </div>

          <div class="text-body2">
            Click the ModirumID push notification on your mobile or tablet to authenticate your identity and authorise this transaction. If your mobile or tablet is not displaying the ModirumID push notification, open ModirumID manually, generate a "Manual OTP" and input the 8 digit passcode into the space provided above.
          </div>
        </div>
      </template>

    </div>
</template>

<script>
const TYPE_TRANSACTIONS = {
  OUTGOING: 'OUTGOING',
  INTERNAL: 'INTERNAL'
}

export default {
  name: 'transaction-card',

  props: {
    model: Object
  },

  methods: {
    authoriseLater () {
      console.log('later')
    },

    authorise () {
      this.status = 'AUTHORISED'
    }
  },

  computed: {
    typeOfTransaction () {
      return this.model && (this.model.externalAccountHolderDetails || this.model.externalAccountHolderDetails) ? TYPE_TRANSACTIONS.OUTGOING : TYPE_TRANSACTIONS.INTERNAL
    },

    date () {
      return this.model ? this.model.executionDate : null
    },

    recipient () {
      if (this.typeOfTransaction === TYPE_TRANSACTIONS.OUTGOING) {
        const externalAccount = this.model ? this.model.externalAccountHolderDetails : null
        return externalAccount ? externalAccount.name : null
      } else {
        return this.model ? this.model.debtorDisplayName : null
      }
    },

    type () {
      return this.typeOfTransaction === TYPE_TRANSACTIONS.OUTGOING ? 'Outgoing' : 'Internal'
    },

    valueDate () {
      return this.model ? this.model.valueDate : null
    },

    remitterName () {
      return 'Capitan Nemo'
      // if (this.typeOfTransaction === TYPE_TRANSACTIONS.OUTGOING) {
      //   return this.model ? this.model.debtorDisplayName : null
      // } else {
      //   return 'CURRENT ACCOUNT NAME'
      // }
    },

    remitterIBAN () {
      return 'CURRENT ACCOUNT IBAN'
    },

    recipientName () {
      return 'NAME'
    },

    recipientEmail () {
      return 'EMAIL'
    }
  },

  data () {
    return {
      opened: false,
      authoriseActive: false,
      TYPE_TRANSACTIONS: TYPE_TRANSACTIONS,
      passcode: null,
      status: 'PENDING'
    }
  }
}
</script>



<style lang="scss" scoped>
@import "../../styles/colors.scss";
@import '../../styles/bulma/variables.scss';
@import '../../styles/typography.scss';

.transaction-card {
  background: white;
  margin: 8px 0;
  padding: 8px 32px 32px;
  border-radius: 6px;
  color: $text-color;

  &:last-child {
    margin-bottom: 32px;
  }

  .info-row {
    padding: 14px 0;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);

    * {
      @extend .text-body1;
    }

    .level {
      padding: 0;
      margin: 0;
    }

    .date {
      display: flex;
      align-items: center;
    }

    .recipient {
      margin-left: 52px;
    }

    .type {
      margin-left: 74px;
    }

    .refNumber {
      margin-left: 70px;
    }

    .level-right {
      margin-left: auto;
    }
  }

  .columns {
    display: flex;
    align-items: center;
    margin: 0;

    .column {
      padding: 0;
    }
  }

  .content {
    padding: 24px 0;

    .transaction-info {
      flex-direction: column;
      align-items: flex-start;

      .info {
        margin-top: 16px;
      }
    }

    .users-info {
      flex-direction: column;
      align-items: flex-end;

      .profile {
        color: #0087ff;
        margin-top: 8px;
      }
    }
  }

  .controls {
    display: flex;

    button.primary {
      margin-left: auto;
    }
  }

  .auth-container {
    padding-top: 24px;
    border-top: 1px solid rgba(0, 0, 0, 0.12);

    .columns.inputs {
      flex-wrap: wrap;
    }

    .column.passcode {
      display: flex;
      align-items: center;

      h5 {
        padding-right: 24px;
      }
    }

    .text-body2 {
      margin-top: 32px;
    }

    .columns + .columns {
      margin-top: 32px;
    }

    .column + .column {
      margin-left: 16px;
    }

    button.column {
      padding: 0 25px;
    }
  }

  .mobile-info-row {
    flex-direction: column;
    padding: 10px 12px;
    
    &.pending {
      border-left: 3px solid #ffcc00;
      padding-left: 9px;
    }

    .column {
      width: 100%;
      flex: 1;
      display: flex;
      justify-content: space-between;
      @extend .text-body1;

      + .column {
        padding-top: 4px;
      }

      .status.pending {
        color: #ffcc00;
      }
    }
  }
}


@media (max-width: $tablet - 1px) {
  .transaction-card {
    padding: 8px 0 12px 0;

    .level.content {
      background: #f3f5f8;
      padding: 12px 12px 18px;
      margin-bottom: 16px;
      max-height: initial;

      &.closed {
        padding: 12px;
        max-height: 48px;
        overflow-y: hidden; 
      }

      .row {
        display: flex;
        cursor: pointer;
        align-items: center;
        justify-content: space-between;
        padding-right: 8px;

        .arrow {
          overflow: hidden;
          width: 10px;
          height: 10px;
          border-top: 3px solid #a9a9a9;
          border-left: 3px solid #a9a9a9;
  
          &.up {
            transform: rotate(45deg);
            margin-top: 5px;
          }
  
          &.down {
            transform: rotate(225deg);
            margin-bottom: 5px;
          }
        }
      }

      .details {
        margin: 16px 0 24px;
      }
    }

    .controls {
      justify-content: center;

      button.main-btn {
        margin-left: initial;
        width: 280px;
      }
    }

    .auth-container {
      padding: 10px 12px 18px;

      .columns.inputs {
        flex-direction: column;

        .column {
          flex: 1;
        }

        .column + .column {
          margin-top: 24px;
        }
      }
    }
  }
}
</style>



