<template>
  <div class="all-accounts">
    <template v-for="group in groups">
      <collapse :selected="true">
        <div slot="collapse-header">
          {{ group.title }}<i></i>
        </div>
        <div slot="collapse-body">
          <AccountsList :type="group.type"/>
        </div>
      </collapse>
    </template>
  </div>
</template>

<script>
  import Currency from '@/components/mixin/currency'
  import Collapse from '@/components/common/Collapse.vue'
  import AccountsList from './AccountsList.vue'

  export default {
    name: 'all-accounts-section',
    mixins: [Currency],
    components: {
      AccountsList,
      Collapse
    },
    computed: {
      groups () {
        const accounts = this.$store.getters.getAccounts()
        const grouped = {}
        const titles = {
          CURRENT: 'Current Accounts',
          SAVINGS: 'Saving Accounts',
          FIXED_TERM: 'Term Deposit Accounts'
        }
        const types = {
          CURRENT: 'current',
          SAVINGS: 'savings',
          FIXED_TERM: 'term'
        }
        for (let account of accounts) {
          if (!grouped[account.accountType]) {
            grouped[account.accountType] = {}
            grouped[account.accountType].list = []
            grouped[account.accountType].title = titles[account.accountType]
            grouped[account.accountType].type = types[account.accountType]
          }
          grouped[account.accountType].list.push(account)
        }
        return grouped
      }
    }
  }
</script>



