<template>
    <div class="section header-section is-paddingless" :class="{menuOpen}">
        <div class="container">
            <div class="level is-mobile is-marginless is-hidden-desktop" >
                <div class="level-left">
                    <router-link tag="div" to="/" class="level is-mobile is-paddingless">
                        <svgicon width="24" height="24" name="language" class="icon is-20px"></svgicon>
                        <span class="text-body2">EN</span>
                    </router-link>
                </div>
                <div class="level-item has-text-centered">
                    <h3 class="title">Demo Bank</h3>
                </div>
                <a class="level-right menu-close" @click="toggleMenu">
                  <transition 
                  name="expand">
                    <div class="nav-icon3" v-if="!menuOpen">
                      <span></span>
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                    <div class="nav-icon3 open" v-if="menuOpen">
                      <span></span>
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                  </transition>

                    <!-- <svgicon name="menu" width="24" height="24"></svgicon> -->
                    <!-- <svgicon name="close" width="24" height="24"></svgicon> -->
                </a>
            </div>
            <div class="level is-mobile is-marginless is-hidden-touch">
                <div class="level-left">
                    <h3 class="title">Demo Bank</h3>
                </div>
                <div class="level-right">
                    <top-desktop-navigation></top-desktop-navigation>
                </div>
            </div>
            <div class="menu">
                <div class="top-message">
                    <h5 class="greeting">Good morning, <br>Alesha Tomkiewich</h5>
                    <div class="text">Your last visit was on</div>
                    <div class="visit-date">Fri Aug 25 2017 11:10 GMT+0300</div>
                </div>

                <ul class="nav main-nav">
                    <router-link tag="li" to="/">
                        <svgicon name="accounts"></svgicon>
                        Accounts
                    </router-link>
                    <router-link tag="li" to="/payments">
                        <svgicon name="payments"></svgicon>
                        Payments
                    </router-link>
                    <router-link tag="li" to="/e-wallet">
                        <svgicon name="e-wallet"></svgicon>
                        Kartiera E-Wallet
                    </router-link>
                    <router-link tag="li" to="/standing-orders">
                        <svgicon name="calendar"></svgicon>
                        Standing Orders
                    </router-link>
                    <router-link tag="li" to="/pending-transactions">
                        <svgicon name="pending_transaction"></svgicon>
                        Pending Transactions
                    </router-link>
                    <router-link tag="li" to="/internal-transfers">
                        <svgicon name="internal_transfer"></svgicon>
                        Internal Transfers
                    </router-link>
                    <router-link tag="li" to="/add-account">
                        <svgicon name="additional_acc"></svgicon>
                        Open Additional Accounts
                    </router-link>
                    <router-link tag="li" to="/claim-funds">
                        <svgicon name="claim_funds"></svgicon>
                        Claim Funds
                    </router-link>
                    <router-link tag="li" to="/mailbox">
                        <svgicon name="mailbox"></svgicon>
                        Mailbox
                    </router-link>
                    <div class="line"></div>
                </ul>

                <BottomMobileNavigation/>

                <ProfileMobileNavigation/>
            </div>
        </div>
    </div>

</template>

<script>
import TopDesktopNavigation from '@/components/navigation/Top.vue'
import BottomMobileNavigation from '@/components/navigation/BottomMobile.vue'
import ProfileMobileNavigation from '@/components/navigation/ProfileMobile.vue'

export default {
  name: 'header-panel',

  components: {
    BottomMobileNavigation,
    ProfileMobileNavigation,
    TopDesktopNavigation
  },

  props: {
    parentToggle: Function
  },

  watch: {
    menuOpen (value) {
      if (this.parentToggle) {
        this.parentToggle()
      }
    }
  },

  methods: {
    toggleMenu () {
      this.menuOpen = !this.menuOpen
    }
  },

  data () {
    return {
      menuOpen: false
    }
  }
}
</script>

<style lang="scss" scoped>
    @import "../styles/colors.scss";
    @import "../styles/bulma/variables.scss";

  .nav-icon3 {
    width: 16px;
    height: 12px;
    position: relative;
    transform: rotate(0deg);
    transition: 2s ease-in-out;
    cursor: pointer;

    span {
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      background: white;
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
      transition: transform 2s ease-in-out;

      &:nth-child(1) {
        top: 0px;
      }

      &:nth-child(2),
      &:nth-child(3) {
        top: 5px;
      }

      &:nth-child(4) {
        top: 10px;
      }
    }

    &.open {
      span {
        transition: transform 2s ease-in-out;
      }
      span:nth-child(1),
      span:nth-child(4) {
        top: 3px;
        width: 1%;
        left: 50%;
      }

      span:nth-child(2) {
        transform: rotate(45deg);
      }
    }
  }

.header-section {
    background: $header-color;

    &.menuOpen {
        display: flex;
        flex-direction: column;
        height: 100vh;
        overflow: scroll;
        -webkit-overflow-scrolling:touch;

        .container {
            width: 100%;
        }
        
        .level {
            background: $header-color;
        }

        .menu {
            display: block;
            overflow-y: scroll;
            height: 100%;
        }
    }

}

.level {
    display: flex;
    height: 56px;
    color: #fff;
    padding: 14px 16px;

    .menu-close {
        color: #fff;
    }

    .level-item h3.title { 
      font-weight: 600; 
    }

    .level-left .svg-icon {
        margin-right: 6px;
    }
}

@media (min-width:1023px) {
    .level {
        padding: 14px 0;
        height: 64px;
    }
}


.menu {
    display: none;
    background: #fff;
    // min-height: 100vh;


    .top-message {
        padding: 15px;
        background: $light-gray;
        .greeting {
            margin-bottom: 8px;
        }
        .text,
        .visit-date {
            opacity: 0.7;
            font-size: 13px;
            line-height: 1.54;
        }
    }

    .main-nav {
        padding: 16px;

        li {
            padding: 12px 0;
            opacity: .7;
            
        } 
    }
    

    .main-nav li.router-link-active,
    .profile-nav li.router-link-active {
        color: $purple-blue;
        // display: flex;
    }

    .main-nav .svg-icon,
    .profile-nav .svg-icon {
        width: 24px;
        height: 24px;
        margin-right: 14px;
    }

    .main-nav .svg-icon path {
        fill: red;
    }
}

@media (max-width: $desktop) { 
  .menu .main-nav li { 
    font-size: 16px; 
    line-height: 24px; 
  } 
} 


.menuOpen {
    width: 100%;
    position: absolute;
    z-index: 10;
}
</style>




