<template>
  <div class="accounts-list response">
    <table class="table">
      <thead>
      <tr>
        <th v-for="field in header">
          <span class="title" @click="sortBy(field)">{{ field.title }}</span>
          <span class="sort" :class="sortType(field.name)">
            <span class="asc"></span>
            <span class="desc"></span>
          </span>
        </th>
      </tr>
      </thead>
      <tbody>
      <template v-for="v in list">
        <tr>
          <td v-for="field in header">
            <span v-if="field.name === 'balance'" :class="currencyClass(v[field.name])">{{ formatCurrency(v[field.name], v.currency) }}</span>
            <template v-else>
              <router-link :to="'/account/' + v.id" v-if="field.name === 'iban'">{{ v.iban }}</router-link>
              <template v-else>{{ v[field.name] }}</template>
            </template>
          </td>
        </tr>
      </template>
      </tbody>
    </table>
  </div>
</template>

<script>
  import Moment from 'moment'
  import Currency from '@/components/mixin/currency'
  import Multisort from 'multisort'

  export default {
    name: 'accounts-list',
    mixins: [Currency],
    props: {
      type: {
        type: String,
        required: true
      }
    },
    data () {
      return {
        sortCriteria: null
      }
    },
    computed: {
      header () {
        const header = []
        header.push({
          name: 'iban',
          title: 'IBAN'
        })
        if (this.type !== 'term') {
          header.push({
            name: 'currency',
            title: 'Currency'
          })
        } else {
          header.push({
            name: 'maturityDate',
            title: 'Maturity'
          })
        }
        if (this.type === 'current' || this.type === 'savings') {
          header.push({
            name: 'name',
            title: 'Alias'
          })
        }
        if (this.type === 'term' || this.type === 'savings') {
          header.push({
            name: 'applicableInterestRate',
            title: 'Interest'
          })
        }
        header.push({
          name: 'owner',
          title: 'Account owner'
        })
        header.push({
          name: 'balance',
          title: 'Balance'
        })
        return header
      },
      list () {
        const r = this.$store.getters.getAccounts(this.type).map(v => {
          v.owner = v.accountHolders[0].displayName
          return v
        })
        const list = JSON.parse(JSON.stringify(r))
        if (!this.sortCriteria) {
          return this.formatList(list)
        }
        return this.formatList(Multisort(list, this.sortCriteria))
      }
    },
    methods: {
      sortBy (field) {
        const sortType = this.sortType(field.name)
        let desc
        console.log(sortType)
        if (sortType === null) {
          desc = false
        } else {
          desc = sortType === 'asc'
        }
        this.sortCriteria = [(desc ? '~' : '') + field.name]
      },
      sortType (fieldName) {
        if (this.sortCriteria) {
          let i = this.sortCriteria.indexOf(fieldName)
          if (i === -1) {
            i = this.sortCriteria.indexOf('~' + fieldName)
            if (i !== -1) {
              return 'desc'
            }
            return null
          } else {
            return 'asc'
          }
        } else {
          return null
        }
      },
      maturityDateFormat (v) {
        return Moment(v).format('DD.MM.YYYY')
      },
      applicableInterestRateFormat (v) {
        return v + '%'
      },
      formatList (list) {
        for (let field of this.header) {
          let method = field.name + 'Format'
          if (this[method]) {
            list.map(item => {
              item[field.name] = this[method](item[field.name])
              return item
            })
          }
        }
        return list
      }
    }
  }
</script>

<style lang="scss">
  @import "../../../styles/colors.scss";

  .accounts-list .table {
    th {
      padding-right: 25px;
      white-space: nowrap;
      span.title {
        user-select: none;
        cursor: pointer;
        &:hover {
          border-bottom: 1px dotted $text-color;
        }
      }
    }
    th:last-child,
    td:last-child {
      text-align: right;
    }
  }

  .sort {
    height: 0;
    margin-left: 7px;
    position: relative;
    span {
      display: inline-block;
      position: absolute;
    }
    &.asc {
      .desc {
        display: none;
      }
    }
    &.desc {
      .asc {
        display: none;
      }
    }
    &.asc {
      .asc {
        top: 5px;
        border-left-width: 4px;
        border-right-width: 4px;
        border-top-width: 5px;
      }
    }
    &.desc {
      .desc {
        top: 5px;
        border-left-width: 4px;
        border-right-width: 4px;
        border-top-width: 5px;
      }
    }
    .asc {
      top: 9px;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-top: 4px solid rgba(0, 0, 0, 0.4);
    }
    .desc {
      top: 1px;
      border-left: 3px solid transparent;
      border-right: 3px solid transparent;
      border-bottom: 4px solid rgba(0, 0, 0, 0.4);
    }
    &.both {
      .desc {
      }
    }
  }

  @media (max-width: 1024px) {
    .accounts-list .table {
      td:first-child {
        display: block;
      }
      td:nth-child(2) {
        display: none;
      }
      td:nth-child(3) {
        display: block;
        padding: 3px 0 10px 0;
      }
      td:nth-child(4) {
        font-size: 12px;
      }
      td:nth-child(5) {
        float: right;
      }
    }
  }
</style>



