<template>
  <div class="column">
    <div class="breadcrumbs text-caption"><a href="#">Demo Bank</a> > <a href="#">Standing Orders</a></div>

    <div class="level name">
      <h1 class="level-left">Standing Orders</h1>
      <button class="main-btn primary level-right" @click="createStandingOrder">Create standing order</button>
    </div>

    <Tabs
      :options="tabs"
      :selected="selectedSection"
      @change="tabChanged"
    />

    <div class="white-card with-tabs">
      <template v-if="loading">
        <Preloader/>
      </template>

      <template v-else>
        <div class="sorting-row columns">
          <div class="column is-narrow ref">Ref</div>
          <div class="column is-narrow name">Beneficiary Name</div>
          <div class="column is-narrow narrative">Standing Order Narrative</div>
          <div class="column is-narrow amount">Amount and Frequency</div>
        </div>

        <div class="order columns"
          v-for="order in orders"
          :key="order.id">
          <div class="column is-narrow ref">{{order.id}}</div>
          <div class="column is-narrow name">{{order.transactionTemplate.source.fullName}}</div>
          <div class="column is-narrow narrative">N/A</div>
          <div class="column is-narrow amount">{{order.frequency.value}} <span>{{order.frequency.type}}</span></div>
        </div>

        <paginator
          :params="params"
          :onPageChange="onPageChange"
          :onLimitChange="onLimitChange"
          :cssClass="'whiteBackground'"
          :needSelector="true"
          v-if="params"
        />
      </template>
    </div>
  </div>
</template>

<script>
import Tabs from '@/components/common/Tabs'
import Types from '@/store/types.js'
import Preloader from '@/components/common/Preloader'
import Paginator from '@/components/common/Paginator.vue'

export default {
  name: 'StandingOrdersScene',

  components: {
    Tabs,
    Preloader,
    Paginator
  },

  watch: {
    selectedSection () {
      this.fetch()
    },

    params (val) {
      console.log('params', val)
    }
  },

  methods: {
    createStandingOrder () {
      console.log('1')
    },

    tabChanged (id) {
      this.$router.push(`/standing-orders/${id}`)
    },

    fetch () {
      this.$store.dispatch(Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_REQUEST, {
        candidateId: 1,
        accountId: 1,
        pageNumber: this.pageNumber,
        pageSize: this.pageSize,
        sort: this.sort,
        status: this.selectedSection
      })
    },

    onPageChange (page) {
      this.$store.dispatch(Types.STANDING_ORDERS_ON_ACCOUNT_CHANGE_PARAMS, {
        pageNumber: page
      })

      this.fetch()
    },

    onLimitChange (pageSize) {
      this.$store.dispatch(Types.STANDING_ORDERS_ON_ACCOUNT_CHANGE_PARAMS, {
        pageSize: pageSize
      })

      this.fetch()
    }
  },

  computed: {
    selectedSection () {
      return this.$route.params.section || 'active'
    },

    pageNumber () {
      return this.params ? this.params.pageNumber : null
    },

    pageSize () {
      return this.params ? this.params.pageSize : null
    },

    params () {
      return this.state.params
    },

    loading () {
      return this.state.loading
    },

    orders () {
      return this.state.data || []
    },

    state () {
      return this.$store.state.standing
    }
  },

  data () {
    return {
      tabs: [
        {
          id: 'active',
          title: 'Active'
        },
        {
          id: 'archived',
          title: 'Archived'
        },
        {
          id: 'pending',
          title: 'Pending'
        }
      ]
    }
  },

  beforeMount () {
    this.fetch()
  }
}
</script>


<style lang="scss" scoped>
@import '../../styles/typography.scss';

  .columns {
    margin: 0;
  }

  .level.name {
    h1 {
      margin-bottom: 0;
    }
    
    margin-bottom: 32px;
  }

  .white-card {
    padding: 24px 32px 32px;

    .sorting-row {
      border-bottom: 2px solid rgba(0, 0, 0, 0.12);

      * {
        @extend .text-body1;
        opacity: .7;
      }
  

    }
    .column {
      padding: 16px 0;

      &.ref {
        width: 14%;
      }

      &.name {
        width: 30%;
      }

      &.narrative {
        width: 35%;
      }

      &.amount {
        width: 21%;
      }
    }

    .order .column {
      border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    }
  }

</style>



