'use strict'

import Types from '@/store/types.js'
import StandingApi from '@/api/standingOrders.js'

const state = {
  data: {},
  loading: false,
  error: null,
  params: {
    pageNumber: 1,
    pageSize: 10,
    totalPages: 1,
    totalItems: 1
  },
  standingOrderStatusCount: []
}

const actions = {
  [ Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_REQUEST ] ({ commit, dispatch }, params) {
    commit(Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_REQUEST)

    StandingApi.getOrdersOnAccount(params)
    .then(data => commit(Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_SUCCESS, data))
    .catch(err => commit(Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_FAILURE, err))
  },

  [ Types.STANDING_ORDERS_ON_ACCOUNT_CHANGE_PARAMS ] ({commit}, params) {
    commit(Types.STANDING_ORDERS_ON_ACCOUNT_CHANGE_PARAMS, params)
  }
}

const mutations = {
  [ Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_REQUEST ] (state) {
    state.loading = true
  },

  [ Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_SUCCESS ] (state, {data, pageNumber, pageSize, totalPages, totalItems, standingOrderStatusCount}) {
    state.data = data
    state.standingOrderStatusCount = standingOrderStatusCount
    Object.assign(state.params, { pageNumber, pageSize, totalPages, totalItems })
    state.error = null
    state.loading = false
  },

  [ Types.STANDING_ORDERS_ON_ACCOUNT_FETCH_FAILURE ] (state, err) {
    state.error = err
    state.loading = false
  },

  [ Types.STANDING_ORDERS_ON_ACCOUNT_CHANGE_PARAMS ] (state, params) {
    console.log(params, 'params')
    console.log(Object.assign({}, state.params), 'state.params')
    state.params = Object.assign(state.params, params)
    console.log(Object.assign({}, state.params), 'state.params')
  }
}

export default {
  state,
  actions,
  mutations
}



